// src/i18n.js

import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetctor from "i18next-browser-languagedetctor"

// src/i18n.js

i18n.use(LanguageDetctor).use(initReactI18next).init({
  debug: true,
  lng: "en",
  resources:{
    en:{
      translation:{
        greeting: "Hello, Welcome!"
      },

    },
    hi:{
      translation:{
        greeting: "नमस्ते, आपका स्वागत है!"
      }
    }
  }
})

const resources = {
  en: {
    translation: {
      comment: "Comment",
      edit: "Edit",
      delete: "Delete",
      save: "Save",
      like: "Like",
      dislike: "Dislike",
      translate: "Translate",
      commented: "commented",
      pleaseLogin: "Please login to like/dislike the comment",
      editCommentPlaceholder: "Edit your comment...",
      commentType: "Please type your comment",
    },
  },
  es: {
    translation: {
      comment: "Comentario",
      edit: "Editar",
      delete: "Eliminar",
      save: "Guardar",
      like: "Me gusta",
      dislike: "No me gusta",
      translate: "Traducir",
      commented: "comentado",
      pleaseLogin: "Por favor inicie sesión para dar me gusta/no me gusta",
      editCommentPlaceholder: "Edita tu comentario...",
      commentType: "Por favor, escribe tu comentario",
    },
  },
};

export default resources;
